.BurgerComponents {
}


.BurgerComponents__locked {
}

.BurgerComponents__unlocked {
    position: relative;
    min-height: 480px;
    max-height: 480px;
    overflow: hidden;
    overflow-y: scroll;
    padding-left: 0;
    margin: 0;
}

.BurgerComponents__unlocked::-webkit-scrollbar{
    width: 8px;
    background: #2F2F37;
}

.BurgerComponents__unlocked::-webkit-scrollbar-track {
    background: #2F2F37;
    border: 1px dashed #4C4CFF;
}

.BurgerComponents__unlocked::-webkit-scrollbar-thumb{
    background: #8585AD;
    border: 3px solid #8585AD;
    height: 292px;
}